.clockpicker-minutes.clockpicker-dial-out {
    -webkit-transform: scale(.8, .8);
    -moz-transform: scale(.8, .8);
    -ms-transform: scale(.8, .8);
    -o-transform: scale(.8, .8);
    transform: scale(.8, .8)
}

.clockpicker-canvas {
    -webkit-transition: opacity 300ms;
    -moz-transition: opacity 300ms;
    -ms-transition: opacity 300ms;
    -o-transition: opacity 300ms;
    transition: opacity 300ms
}

.clockpicker-canvas-out {
    opacity: .25
}

.clockpicker-canvas-bearing {
    stroke: none;
    fill: rgba(0, 77, 64, .75)
}

.clockpicker-canvas-fg {
    stroke: none;
    fill: rgba(0, 77, 64, 0)
}

.clockpicker-canvas-fg.active {
    fill: rgba(0, 77, 64, .5)
}

.clockpicker-canvas-bg, .clockpicker-canvas-bg-trans {
    fill: rgba(0, 150, 136, .25)
}

.clockpicker-canvas-bg {
    stroke: none
}

.clockpicker-canvas line {
    stroke: rgba(0, 150, 136, .25);
    stroke-width: 1;
    stroke-linecap: round
}

.clockpicker-am-pm-block {
    margin-top: -10px;
    width: 100%;
    height: 50px
}

.clockpicker-button.am-button {
    height: 45px;
    width: 45px;
    float: left
}

.clockpicker-button.pm-button {
    height: 45px;
    width: 45px;
    float: right
}

.btn-floating.btn-flat:focus, .btn-floating.btn-flat:visited {
    background-color: transparent
}

.btn-floating.btn-flat:hover {
    box-shadow: none
}

.btn-floating.btn-flat {
    padding: 0;
    background: #4285F4
}

.btn-floating.btn-flat:focus, .btn-floating.btn-flat:hover {
    background-color: #5a95f5 !important
}

.btn-floating.btn-flat.active {
    background-color: #0b51c5 !important
}

.btn-floating.btn-flat:active {
    background-color: rgba(0, 150, 136, .5)
}

.picker__footer .clockpicker-button {
    margin: 15px auto auto
}

.picker__footer .clockpicker-button:focus {
    background-color: transparent
}

.picker__footer .clockpicker-button:active {
    background-color: rgba(0, 150, 136, .25)
}

.darktheme .picker__box {
    background-color: #212121
}

.darktheme .clockpicker-plate, .darktheme .picker__date-display {
    background-color: transparent
}

.darktheme .clockpicker-display, .darktheme .clockpicker-span-am-pm {
    color: #fff
}

.darktheme #click-am.text-primary, .darktheme #click-pm.text-primary,
.darktheme .clockpicker-span-hours.text-primary, .darktheme .clockpicker-span-minutes.text-primary {
    color: #ff4081
}

.darktheme .clockpicker-tick {
    color: #fff;
    background-color: rgba(255, 64, 129, 0)
}

.darktheme .btn-floating.btn-flat.active, .darktheme .clockpicker-tick.active,
.darktheme .clockpicker-tick:hover {
    background-color: rgba(255, 64, 129, .25)
}

.darktheme .clockpicker-canvas-bearing {
    fill: #fff
}

.darktheme .clockpicker-canvas-fg {
    fill: rgba(255, 64, 129, 0)
}

.darktheme .clockpicker-canvas-fg.active {
    fill: rgba(255, 64, 129, .5)
}

.darktheme .clockpicker-canvas-bg, .darktheme .clockpicker-canvas-bg-trans {
    fill: rgba(255, 64, 129, .25)
}

.darktheme .clockpicker-canvas line {
    stroke: rgba(255, 64, 129, .25)
}

.darktheme button {
    color: #fff
}

.darktheme .btn-floating.btn-flat:active {
    background-color: rgba(255, 64, 129, .5)
}

.darktheme .picker__footer .clockpicker-button:active {
    background-color: rgba(255, 64, 129, .25)
}

#click-am.text-primary, #click-pm.text-primary, .clockpicker-span-hours.text-primary,
.clockpicker-span-minutes.text-primary {
    -webkit-animation-name: pulse;
    animation-name: pulse;
    color: #fff !important
}
